'use strict';

angular.module('Tasks').directive('avatar', function () {
  'use strict';

  return {
    restrict: 'A',
    scope: false,
    link: function link(scope, elm, attr) {
      return attr.$observe('userid', function () {
        if (attr.userid) {
          return elm.avatar(attr.userid, attr.size);
        }
      });
    }
  };
});