'use strict';

angular.module('Tasks').factory('Status', [function () {
  'use strict';

  var Status;
  Status = function () {
    function Status() {
      this._$status = {
        addingList: false,
        focusTaskInput: false
      };
    }

    Status.prototype.getStatus = function () {
      return this._$status;
    };

    return Status;
  }();
  return new Status();
}]);